@import "imports/owl-carousel";
@import "imports/owl-theme";
@import "imports/owl-transitions";
html{
	position: relative;
	min-height: 100%;
}
body{
	margin-bottom: 120px;
}
$color-light-gray: #ebebef;
$color-medium-gray: #b0b2c3;
$color-secondary: #e94018;
$color-dark-gray: #5e5f68;
$color-primary: #293889;
$color-highlight: #f6f6f6;
a{
		transition: all 0.2s;
		color: #5e5f68;
		&:hover{

		}
}
.nav{
	>li>a:focus, >li>a:hover, li.open>a, li.open>a:focus, li.open>a:hover{
		background-color: $color-highlight;
	}
}
.navbar{
	background-color: $color-light-gray;
	border-top: 5px solid $color-medium-gray;
	height: 90px;
	box-sizing: content-box;
	box-shadow: 0 2px 10px -3px rgba(0,0,0,.5);
	transition: all 0.2s;
	.navbar-brand{
		height: 90px;
		max-width: 120px;
		&, img{
			transition: all 0.2s;
		}
		img{
			display: inline-block;
			max-height: 60px;
			width: auto;
		}
	}
	li{
		&.current{
			a{
				color: $color-dark-gray;
			}
		}
	}
	&.scaled{
		height: 50px;
		.navbar-brand{
			padding: 6px 15px;
			height: 51px;
		}
		img{
			max-height: 40px;
		}
	}
	@media screen and (max-width:400px){
		.navbar-brand img{
			height: 41px;
		}
	}
	@media screen and (max-width:767px){
		.navbar-collapse{
			background-color: $color-light-gray;
			box-shadow: 0 2px 10px -3px rgba(0,0,0,.5);
		}
	}
	@media screen and (min-width:767px){
		a{
			font-weight: 300;
			font-size: 20px;
		}
		&.scaled{
			.navbar-nav{
				>li:not(.languages)>a{
					padding-top: 20px;
					line-height: 17px;
				}
			}
		}
		.navbar-nav{
			>li{
				&.current{
					a{
						border-color: $color-primary;
					}
				}
				>a{
					padding-top: 31px;
					line-height: 45px;
					border-bottom: 1px solid transparent;
					&:hover, &.active, &:focus, &:active{
						border-color: $color-primary;
					}
				}
			}
		}
		li.languages{
			display: inline-block;
			vertical-align: top;
			>a{
				font-size: 12px;
				font-weight: 700;
				padding: 0;
				display: inline-block;
				line-height: 35px;
				margin: 0 5px 0;
				color: $color-medium-gray;
				position: relative;
				&:hover, &:focus, &.active{
					color: $color-dark-gray;
					background-color: transparent;
				}
				&.active:before{
					content: "";
					position: absolute;
					width: 0;
					top: 0;
					right: 0;
					left: 0;
					margin: auto;
					border: 4px solid transparent;
					border-top-color: $color-medium-gray;
					border-bottom: none;				
				}
			}
		}
		ul.dropdown-menu{
			left: 0;
			right: auto;
			padding: 0;
			box-shadow: 0 2px 10px -3px rgba(0,0,0,.5);
			border-width: 1px 0 0;
			border-radius: 0;
			border-color: $color-primary;
			margin-top: -1px;
			background-color: $color-highlight;
			a{
				padding: 5px 15px;
				display: block;
				line-height: 1.2;
				color: $color-primary;
				font-weight: 300;
				font-size: 16px;
				&:hover, &:focus{
					background-color: $color-light-gray;
				}
			}
		}
		li a{
			font-size: 18px;
			padding-right: 8px;
			padding-left: 8px;
		}
		li.languages{
			position: absolute;
			right: 22px;
		}
	}
}
.sub-header {
    background-color: $color-medium-gray;
    box-shadow: inset 0 -2px 10px -3px rgba(0,0,0,.5);
		color: $color-dark-gray;
		.page-title{
			font-size: 24px;
		}
		.breadcrumb{
			background-color: transparent;
			padding: 0;
			margin-bottom: 10px;
			>li+li:before{
				content: "\f105";
				color: #333;
				font-family: FontAwesome;
				font-size: 1.2em;
			}
		}
		img{
			position: absolute;
			max-height: 80px;
			width: auto;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
		}
}
li.languages a{
	border-bottom: none !important;
}
.main-header-slider{
	img{
		min-height: 300px;
	}
}
.highlights-slider-text .owl-dots{
	display: none !important;
}
.owl-carousel{
	direction: ltr;
	.slide-text{
		position: absolute;
		color: #fff;
		&.center{
			text-align: center;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			white-space: nowrap;
		}
		.slide-title{
			font-size: 1.7em;
			font-weight: 500;
		}
		.slide-subtitle{
			font-size: 1.5em;
			font-weight: 300;
		}
		@media screen and (max-width: 767px){
			.slide-title{font-size: 1.5em;}
			.slide-subtitle{font-size: 1.1em;}
		}
		@media screen and (max-width: 490px){
			.slide-title{font-size: 1.2em;}
			.slide-subtitle{font-size: 1em;}
		}
		.underlined{
			padding: 10px 0;
			border-bottom: 1px solid;
		}
	}
}
.title-bar{
	background: url(images/dotted-bg.svg) repeat-x left center;
	background-size: 2px 5px;
	position: relative;
	margin: 20px 0 10px;
	>*{
		display: inline-block;
		background-color: #fff;
		margin: 0;
		padding: 0 10px 0 0;
	}
	.show-more{
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		padding: 0 0 0 10px;
	}
	&.text-center{
		>*{
			padding: 0 10px;
		}
	}
}
.widget{
	.content{
		ul{
			list-style: none;
			padding: 0 20px;
			li{
				line-height: 2;
			}
		}
	}
}
footer{
	position: absolute;
	bottom: 0;
	width: 100%;
	margin-top: 30px;
	background-color: $color-dark-gray;
	border-top: 6px solid $color-medium-gray;
	color: $color-light-gray;
	padding-top: 20px;
	padding-bottom: 20px;
	a{
		color: $color-medium-gray;
		&:hover, &:focus{
			text-decoration: none;
			color: $color-light-gray;
		}
	}	
	.languages{
		margin-left: 10px;
		font-size: 11px;
		a{
			margin: 0 0 0 5px;
		}
	}
}
article{
	margin-bottom: 20px;
	.content{
		>img:first-child{
			max-width: 50%;
			height: auto;
		}
		img{
			&.pull-right{
				margin: 20px 0 20px 20px;
			}
			&.pull-left{
				margin: 20px 20px 20px 0;
			}
			&.block{
				width: 100%;
				max-width: 100%;
				margin-bottom: 20px;
			}
		}
	}
}
@media screen and (max-width:990px){
	.row.mobile-reverse{
		display: flex;
		flex-direction: column-reverse;
	}
}
.thumbs-list{
	.item.row{
		margin: 0;
	}
}
.thumbs-list, .thumbs-grid{
	>div{
		padding-bottom: 15px;
		padding-top: 15px;
	}
	.item{
		background: $color-light-gray;
		padding: 10px;
		>a{
			position: relative;
			display: block;
			margin-bottom: 5px;
		}
		p:last-child{
			margin: 0;
		}
	}
}
.thumbs-grid{
	.media-title{
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		text-align: center;
		background-color: rgba(0, 0, 0, 0.5);
		color: #fff;
		font-weight: bold;
		font-size: 18px;
		z-index: 1;
	}
}
.media-object{
	max-width: 100%;
	min-width: 100%;
	height: auto;
	display: block;
}
/*ltr*/
.text-end{
	text-align: right;
}


/*map*/
#map{
	width: 100%;
	height: 400px;
	margin: 20px 0;
}